const styles = {
  card: {
    display: 'flex',
    alignItems: 'flex-start',
    margin: '0 0 20px'
  },
  img: {
    width: '30%',
    flexShrink: '0',
    marginRight: '20px'
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
  },
  title: {
    marginBottom: '.5em',
  },
  description: {
    marginBottom: '1em',
    lineHeight: '1.35'
  },
  gallery: {
    display: 'flex',
    alignItems: 'flex-start',
    flexWrap: 'wrap'
  },
  galleryItem: {
    height: '180px',
    width: 'auto',
    marginRight: '1%',
    marginBottom: '1%'
  }
}

export default styles;



